.header{
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    width: 95%;
}

.header input {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* height: calc(50px + 2vh); */
    font-size: calc(7px + 2vh);
    border: none;
    border-radius: 5px;
    /* margin: 10px; */
    padding: 10px;
    background: whitesmoke;
}

.header input::placeholder {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2px;
    padding: 2px;
}
